<template>
 <div id="app">
    <div class="ui fixed inverted menu vue-color">
      <div class="ui container">
        <a href="#" class="header item">VUE JS CRUD AXIOS</a>
      </div>
    </div>
    <h2 style="margin-top:70px">DATA PRODUK <router-link to="/tambahproduk"><button class="small ui violet button">
  <i class="plus icon"></i>
  Tambah Produk
</button></router-link></h2>
    <div class="ui main container" style="margin-top:45px">
     
    <table class="ui celled table">
  <thead>
    <tr>
    <th>No</th>
    <th>Nama</th>
    <th>Harga</th>
    <th>Kondisi</th>
    <th>Tindakan</th>
  </tr></thead>
  <tbody style="margin: auto;" v-if="mhs.length > 0">
    <tr v-for="mhsdata in mhs" v-bind:key="mhsdata.id">
      <td > -</td>
      <td >{{ mhsdata.nama_produk }} {{ mhsdata.id }}</td>
      <td >{{ mhsdata.harga }}</td>
      <td >{{ mhsdata.kondisi }}</td>
      <td><div class="ui buttons">
  <button class="ui teal button">  <i class="edit icon"></i> Edit</button>
  <div class=""></div>
  <button class="ui red button" @click="deleteData(mhsdata)"><i class="times icon"></i> hapus</button>
</div></td>
    </tr>
  </tbody>
</table>
    </div>
    </div>

</template>

<script>
import axios from 'axios';

export default {
  name: "LogoPatner",
  components: { },
  data() {
    return {
      mhs: []
    };
  },
  mounted() {
    axios
      .get("https://bl.ajisukmo.tech/api/produk")
      .then(res => (this.mhs = res.data.data))
      .catch(err => console.log(err));
  },
  methods: {
  deleteData(mhsdata){
    axios
  .delete(`https://bl.ajisukmo.tech/api/delete/${mhsdata.id}`)
    .then(res => { console.log(res) })
    .catch(err => { console.error(err)})
  }
  }
  
}
</script>

<style>
.vue-color {
  background: #41b883 !important;
}
</style>
